@charset "utf-8";
h1{
    text-align: center;
}

.contents {
    width: fit-content;
    margin: auto;
}

.contents>h1{
    margin-top: 60px;
    text-align: center;
    margin-bottom: 95px;
    font-size: 32px;
}

.Search-box{
    margin: 110px auto 140px;
}

.contents>ul{
    width: fit-content;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: auto;
    text-align: center;
    justify-items: center;
    align-items: center;
    gap: 136px;
    row-gap: 110px;
}

.contents>ul>li>a>.txt {
    font-size: 32px;
    font-weight: 500;
    text-shadow:
        -1px -1px 0 black,
        1px -1px 0 black,
        -1px 1px 0 black,
        1px 1px 0 black;
}

.contents>ul>li>a>.imgbox{
    width: 320px;
    height: 160px;
    text-indent: -9999px;
}

.character-search{
    margin: 110px auto 140px;
}