@charset "utf-8";
.isbirthday{
    width: fit-content;
    margin: auto;
    text-shadow: 
    -1px -1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    1px 1px 0 #000;
}
.countdown-wrap{
    width: fit-content;
    margin: auto;
    text-shadow: 
    -1px -1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    1px 1px 0 #000;
}
.countdown-wrap>h2{
    font-size: 48px;
    text-align: center;
    margin-bottom: 133px;
}
.countdown-wrap>div{
    font-size: 48px;
    text-align: center;
}
.countdown-wrap>div>.time{
    display: flex;
    gap: 105px;
    margin-bottom: 88px;
}
.loading{
    text-align: center;
}