@charset "utf-8";
@media (min-width:320px) and (max-width:1080px){
    .search-wrap{
        width: 80%;
        height: 50px;
        border-radius: 50px;
    }
    .search-table{
        width: calc(80%-70px);
        height: 100%;
        font-size: 16px;
        text-indent: 20px;
    }
    .search-btn{
        width: 70px;
        height: 50px;
        font-size: 16px;
    }
}