@charset "utf-8";
@media (min-width:320px) and (max-width:1080px){
    .contents{
        width: 100%;
        height: 100%;
    }
    .Search-box{
        margin: 95px auto 0;
    }
    .contents>ul{
        display: flex;
        flex-direction: column;
        gap: 30px;
    }
    .contents>ul>li>a>.imgbox{
        width: 300px;
        height: 150px;
    }
    .contents>ul>li>a>.txt{
        font-size: 20px;
    }
}