@charset "utf-8";
.search-wrap{
    width: 780px;
    height: 70px;
    border-radius: 70px;
    background: var(--search_box_color);
    position: relative;
}
.search-table{
    width: 650px;
    height: 100%;
    text-indent: 30px;
    outline: none;
    background: none;
    border: none;
    font-size: 24px;
}
.search-btn{
    width: 130px;
    height: 70px;
    border-radius: 70px;
    background: var(--main_color);
    position: absolute;
    top: 0;
    right: 0;
    font-size: 24px;
    color: #fff;
    font-weight: 600;
}