@charset "utf-8";
a{
    width: 100%;
    height: 100%;
    padding: 8px;
}
.Title{
    margin: 180px auto 0;
}
.Search{
    margin: 110px auto;
}
.list-box-wrap{
    width: 100%;
}
.list-box{
    width: fit-content;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: auto;
    text-align: center;
    justify-items: center;
    align-items: center;
    gap: 100px;
    row-gap: 20px;
    font-size: 16px;
    font-weight: 600;
}
.list-box>li:nth-child(1){
    color: var(--versing);
}
.list-box>li:nth-child(1):hover{
    background: var(--versing);
    color: #fff;
}
.list-box>li:nth-child(2){
    color: var(--leo_need);
}
.list-box>li:nth-child(2):hover{
    background: var(--leo_need);
    color: #fff;
}
.list-box>li:nth-child(3){
    color: var(--mmj);
}
.list-box>li:nth-child(3):hover{
    background: var(--mmj);
    color: #fff;
}
.list-box>li:nth-child(4){
    color: var(--vvd);
}
.list-box>li:nth-child(4):hover{
    background: var(--vvd);
    color: #fff;
}
.list-box>li:nth-child(5){
    grid-column: 2;
    grid-row: 2;
    color: var(--wonder_show);
}
.list-box>li:nth-child(5):hover{
    background: var(--wonder_show);
    color: #fff;
}
.list-box>li:nth-child(6){
    grid-column: 3;
    grid-row: 2;
    color: var(--ni_go);
}
.list-box>li:nth-child(6):hover{
    background: var(--ni_go);
    color: #fff;
}