@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&family=Pacifico&display=swap');
body{
    font-family: "Noto Sans KR", sans-serif;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    display: block;
    color: inherit;
}
button{
    border: none;
    background: none;
    cursor: pointer;
}
:root{
    --main_color: #2ed7e1;
    --search_box_color: #d9d9d9;
    --versing: #000;
    --leo_need: #4455dd;
    --mmj: #88DD44;
    --vvd: #ee1166;
    --wonder_show: #ff9900;
    --ni_go: #884499;
}