@charset "utf-8";
.resultpage{
    width: fit-content;
    margin: auto;
}
.resultpage>h2{
    font-size: 36px;
    margin-bottom: 30px;
    text-align: center;
}
.resultpage>p{
    font-size: 24px;
    text-align: center;
}
.resultpage>ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    gap: 130px;
    row-gap: 120px;
}
.resultpage>ul>li{
    font-size: 24px;
    font-weight: 500;
    text-align: center;
    text-shadow: 
    -1px -1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    1px 1px 0 #000;
}
.resultpage>ul>li>a>div{
    width: 320px;
    height: 160px;
    font-size: 16px;
    color: #000;
    text-shadow: none;
    text-indent: -9999px;
}
p{
    text-align: center;
}